.hidden {
  display: none;
}

.thumb {
  position: relative;
  margin-bottom: 14px;
  border-radius: 8px;
  overflow: hidden;
  @media screen and (min-width: 768px) {
      height: 316px;
      min-width: 0;
    }
    @media screen and (min-width: 1440px) {
        height: 256px;
      }
}

.thumb > img {
  width: 100%;
  height: 100%;
}

.book-title {
  text-align: start;
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 1.125;
  color: var(--text-main);
  letter-spacing: -0.64px;
  text-transform: uppercase;
}

.book-item .book-author {
  text-align: start;
  margin-bottom: 0px;
  font-size: 12px;
  line-height: 1.16;
}

.overlay {
  position: absolute;
  display: flex;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 27%;
  justify-content: center;
  align-items: center;
  background-color: var(--color-links);

  transform: translateY(100%);
  transition: transform 250ms var(--timing-function);
}

.thumb:hover .overlay {
  transform: translateY(0);
}

.overlay-text {
  color: var(--text-main);
  font-size: 30px;
  font-weight: 500;
  line-height: 1.28;
  letter-spacing: -0.56px;
  text-transform: uppercase;
  @media screen and (min-width: 768px) {
      font-size: 20px;
    }
    @media screen and (min-width: 1440px) {
        font-size: 14px;
      }
}

.section-books-list li:not(:last-child) {
  margin-bottom: 40px;
}

.section-categories-list {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.accent {
  color: var(--color-accent);
}

.section-categories-title,
.section-books-title {
  margin-bottom: 40px;
  font-size: 32px;
  line-height: 1.19;
  letter-spacing: -1.28px;
  text-transform: capitalize;
  @media screen and (min-width: 768px){
      font-size: 48px;
      line-height: 1.08;
      letter-spacing: -1.92px;
    }
}

.section-categories-list .btn {
  display: block;
  margin-left: auto;
  padding: 12px 26px;
  border-radius: 40px;
  border: 2px solid var(--color-accent);

  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  letter-spacing: -0.14px;
  text-transform: uppercase;
  color: var(--text-main);
  cursor: pointer;
  background-color: var(--body);
  transition: background-color 250ms var(--timing-function);
}

.section-categories-list .btn:hover{
  color: #fff;
  background-color: var(--color-accent);
}

@media screen and (min-width: 768px) {

  .section-books-list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 40px;
    column-gap: 25px;
  }
  .section-books-list li:not(:last-child) {
    margin-bottom: 0;
  }

  .section-books-list li {
    width: calc((100% - 50px) / 3);
  }

}

@media screen and (min-width: 1440px) {

  .section-books-list {
    column-gap: 24px;
  }
  .section-books-list li {
    width: calc((100% - 96px) / 5);
  }
}
