.theme-switch {
  width: 40px;
  height: 20px;
  margin-left: auto;
  position: relative;
}

.theme-switch input {
  display: none;
}

.theme-switch .toggle {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 20px;
  width: 40px;
  height: 20px;
  transition: background-color .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch .toggle:before {
  content: "";
  background-color: #ffffe8;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: transform .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.theme-switch input:checked + .toggle {
  background: linear-gradient(#4f2ee8, #686868);
}

.theme-switch input:checked + .toggle:before {
  transform: translateX(20px);
}

